/* gnav
------------------------------------------------------ */
.gnav {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
  display: block;
  padding: 1em;
  background: $c_key2;
  font-size: min((30/1084)*100vw,20px);
  box-shadow: 0 2px 13px rgb(0 0 0 / 20%);
  font-family: "Gentium Plus", serif;
  font-weight: bold;

}
.gnav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.gnav__item {
  margin: 0 1.25em;
}
.gnav__link {
  display: flex;
  align-items: center;
  text-decoration: none;
  // &::before {
  //   content: "";
  //   display: inline-block;
  //   width: 1.4em;
  //   height: 1.4em;
  //   margin-right: 0.3em;
  //   background: url() center center / contain no-repeat;
  //   vertical-align: middle;
  // }
// アイコン入れるなら↑
  &:hover,
  &:active,
  &[aria-current] {
    color: $c_key3;
    font-weight: bold;
    // &::before {
    //   background-image: url();
    // }
    // ホバーで画像変えるなら↑
  }
}
