/* header
------------------------------------------------------ */
.header {
  margin-top: 70px;
  height: 40vh;
  background-image: url(/assets/img/header_img.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  object-fit: cover;
  position: relative;
  z-index: 0;
  overflow: hidden; /*ブラー効果でボヤけた部分を非表示*/
  @include medium {
    height: 60vh;
  }
}

/*:beforeにぼかし効果を設定する*/
.header:before {
  content: "";
  background: inherit;
  filter: blur(1px);
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1; /*重なり順序を一番下にしておく*/
}

.header__inner {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__txt {
  display: block;
  justify-content: center;
  align-items: center;
  width: 24ch;
  margin-bottom: 80px;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid transparent;
  animation: typing 3s steps(24), caret 0.5s steps(1) infinite;
  // font-family: "Gentium Plus", serif;
  font: bold 200% Consolas, Monaco, monospace;
  font-size: min(6vw, 80px);
  font-weight: bold;
  text-shadow: 2px 2px 0 $c_white;
  a {
    text-decoration: none;
  }
  @include medium {
  }
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes caret {
  50% {
    border-right-color: currentColor;
  }
}
