/* breadcrumb
------------------------------------------------------ */
.breadcrumb {
  position: relative;
  max-width: $width;
  // containerで囲まないためwidthをつける
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: content-box;
  font-size: 12px;
  a {
    &[aria-current] {
      text-decoration: none;
    }
  }
}
.breadcrumb__list {
  position: absolute;
}
.breadcrumb__item {
  display: inline;
  // 横並びにするため
  &:not(:last-child){
    &::after {
      display: inline-block;
      content: "";
      width: .4em;
      height: .4em;
      border-top: 1px solid;
      border-right: 1px solid;
      margin-left: 0.3em;
      margin-right: 0.2em;
      margin-bottom: .2em;
      line-height: 1em;
      transform: rotate(45deg);
    }
  }
}