/* page-title
------------------------------------------------------ */
.section-title {
  margin-top: 200px;
  position: relative;
  & + * {
    margin-top: 40px;
  }
}
.section-title__main {
  display: block;
  text-align: center;
  margin-top: 10px;
  font-size: 30px;
  letter-spacing: 0.1em;
}
.section-title__sub {
  z-index: -1;
  position: absolute;
  font-family: "Gentium Plus", serif;
  top: -0.8em;
  left: -0.2em;
  opacity: 0.1;
  font-size: 6em;
  text-transform: uppercase;
  @include medium {
    font-size: 10em;
    top: -0.9em;

  }
}
.section-title__sub_reverse {
  z-index: -1;
  position: absolute;
  font-family: "Gentium Plus", serif;
  top: -0.8em;
  right: -0.2em;
  opacity: 0.1;
  font-size: 6em;
  text-transform: uppercase;
  @include medium {
    font-size: 10em;
    top: -0.9em;

  }
}
@include medium {
  .section-title {
    &::before {
      width: 60px;
      height: 48px;
    }
    & + * {
      margin-top: 60px;
    }
  }
  .section-title__main {
    font-size: 40px;
  }
}
