/* container
------------------------------------------------------ */
.container {
  max-width: $width;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: content-box;
  &._narrow {
    max-width: $width_md;
    // settingの中間サイズ
  }
}
// 上下の余白はmaincontentsでつけるから
// 横の設定のみ
// 左右パディングは横幅狭めた時の余白確保