/*----------------------------------------
	base
----------------------------------------*/
html {
  scroll-behavior: smooth;
  scroll-padding-top: 150px; /* 固定メニューの高さ分 */
}
body {
  overflow-x: hidden;
  overflow-y: auto;
  color: $c_text;
  font-family: "Noto Sans JP", sans-serif;
  line-height: 1.75;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.5;
}

a {
  color: $c_link;
  text-decoration: underline;
  transition: all 0.3s;
}
a:hover {
  color: $c_hover;
}
img {
  width: 100%;
  height: auto;
  &.self-size {
    max-width: 100%;
  }
}
button {
  -webkit-appearance: none;
  appearance: none;
  display: block;
  width: 100%;
  border: 0;
  font-family: inherit;
  font-weight: inherit;
}
